<template>
  <v-card style="border: 1px solid rgb(0,0,0,0.38);border-radius:8px;">
    <v-card-text class="px-6">
      <div class="d-flex align-center justify-space-between">
        <v-btn rounded
               small
               :color="list[status]==1?'#18A874':'#ACACAC'">{{list[status]==1?'启用':'禁用'}}</v-btn>
        <div v-if="!readonly">
          <v-btn icon
                 @click="edit"
                 class="mr-8">
            <v-icon style="font-size:28px;"
                    class="material-icons">edit</v-icon>
          </v-btn>
          <v-btn icon
                 @click="deleteItem">
            <v-icon style="font-size:28px;"
                    class="material-icons">delete</v-icon>
          </v-btn>
        </div>
        <span v-else></span>
      </div>
      <div>
        <slot :item='list'>
          <v-row>
            <v-col cols="6">
              <mrc_ess_autoCompletePosition v-model="list.post_uuid"
                                            :disabled="true"
                                            :multiple='true'></mrc_ess_autoCompletePosition>
            </v-col>
            <v-col cols="6"
                   class="mt-1">
              <mrc_ess_multiText :label="$t('是否等级高查看等级低')"
                                 :readonly="true"
                                 v-model="levle">
              </mrc_ess_multiText>
            </v-col>
            <v-col cols="12">
              <mrc_ess_autoCompleteStaff v-model="list.exclude_staff_ids"
                                         item-key='staff_id'
                                         :label="$t('排除人员')"
                                         :disabled="true"
                                         :multiple='true'></mrc_ess_autoCompleteStaff>
            </v-col>
            <v-col cols="12">
              <tree v-model="list.function_uuids"
                    :readonly='true'></tree>
            </v-col>
          </v-row>
        </slot>
      </div>
    </v-card-text>
  </v-card>
</template>

<script>
import tree from './tree'
// 视图显示卡片
export default {
  components: { tree },
  props: {
    readonly: {
      type: Boolean,
      default: true
    },
    index: {
      type: Number
    },
    status: {
      type: String,
      default: 'status'
    },
    list: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {

    };
  },
  computed: {
    levle () {
      return this.list.high_grade_switch == 1 ? '是' : '否'
    }
  },
  created () { },
  watch: {},
  mounted () { },
  methods: {
    edit () {
      this.$emit('edit', this.list, this.index)
    },
    deleteItem () {
      this.$emit('deleteItem', this.list, this.index)
    }
  },
};
</script>

<style scoped>
</style>
