<template>
  <v-card>
    <v-card-title>
    </v-card-title>
    <v-card-text class="pl-0 pr-0">
      <div class="top"
           :style="{'max-height':maxHeight+'px',overflowX: 'hidden','backgroundColor':'#F5F5F5'}">
        <!-- indeterminate -->
        <v-treeview dense
                    :item-disabled="readonly?'disabled':''"
                    activatable
                    color='blue'
                    :items="allList"
                    v-model="selectList"
                    @input='treeInput'
                    item-children='sub'
                    selectable
                    selected-color="rgba(0, 0, 0, 0.38)"
                    :item-text='itemText'
                    :item-key='itemKey'>
          <!-- <template v-slot:prepend="{ open }">
            <v-icon>
              {{open ? 'mdi-folder-open' : 'mdi-folder' }}
            </v-icon>
          </template> -->
          <template v-slot:label="{ item }">
            <span style="font-size: 14px;color: rgba(0, 0, 0, 0.78);font-family: Noto Sans CJK TC;font-style: normal;font-weight: normal;">{{item[itemText]}}</span>
          </template>
          <template v-slot:append="{ item }">
            <mrc_ess_svgIcon v-for="(item1,index) in item.client_type_uuid"
                             :key="index"
                             :iconClass="item1"
                             style="font-size: 23px;" />
          </template>

        </v-treeview>
      </div>
    </v-card-text>
  </v-card>
</template>

<script>
/**
 * 
 * 
 * 
 * 
 * @detailStaff 获取员工的详情数据
 * @selectAll  选择列表中的全部员工
 * @getDetail 
 */
import { getTreeList } from '@/service/api/data-access-strategy.js'
export default {
  components: {},
  props: {
    readonly: {
      type: Boolean,
      default: false
    },
    maxHeight: {
      type: String,
      default: '400'
    },
    itemText: {
      type: String,
      default: 'names'
    },
    itemKey: {
      type: String,
      default: 'uuid'
    },
    select: {
      type: String,
    }
  },
  model: {
    prop: 'select',
    event: 'changeSelect'
  },
  data () {
    return {
      allList: [],
      selectList: [],
      open: false
    };
  },
  computed: {
  },
  created () { },
  watch: {
    select: {
      handler (v) {
        if (!v == '') {
          v.indexOf(',') != -1 ? this.selectList = v.split(',') : this.selectList = [v]
        }
        else {
          this.selectList = []
        }
      }, deep: true
    }
  },
  async mounted () {
    await this.getTreeList()
    if (!this.select == '') {
      this.select.indexOf(',') != -1 ? this.selectList = this.select.split(',') : this.selectList = [this.select]
    }
  },
  methods: {
    async getTreeList () {
      if (JSON.parse(localStorage.getItem('moduleTreeList')) && JSON.parse(localStorage.getItem('moduleTreeList')).length > 0) {
        this.allList = JSON.parse(localStorage.getItem('moduleTreeList'))
        this.changeType(this.allList)
      }
      else {
        const res = await getTreeList()
        if (res.code == 200) {
          localStorage.setItem('moduleTreeList', JSON.stringify(res.data))
          this.allList = res.data
          this.changeType(this.allList)
        }
        else {

        }
      }

    },
    treeInput (val) {
      this.$emit('changeSelect', val.length > 0 ? val.join() : '')
    },
    changeType (val) {
      val.map(item => {
        item.disabled = true
        if (!item.client_type_uuid == '') {
          item.client_type_uuid.indexOf(',') != -1 ? item.client_type_uuid = item.client_type_uuid.split(',') : item.client_type_uuid = [item.client_type_uuid]
        }
        if (item.sub && item.sub.length > 0) {
          this.changeType(item.sub)
        }
      })
    },
  },
};
</script>

<style scoped>
.active {
  background-color: red;
}
.spans {
  max-width: 30vw;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.top {
  border: 2px solid #ececec;
  overflow-y: auto;
}
.footer {
  min-height: 350px;
  border: 2px solid #ececec;
  box-shadow: none;
}
.footer_box {
  max-height: 300px;
  overflow-y: auto;
}
.staffactive {
  background-color: #e5eeff;
}
</style>
